@import '../../../../assets/styles/colors';
@import '../../../../assets/styles/mixin';

#transport_page_not_found {
  background-color: $primary-color;
  color: $white-color;
  font-family: 'Poppins', sans-serif;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 101;

  .wrapper {
    width: 260px;

    .logo-wrapper {
      img {
        width: 180px;
      }
    }
    .title {
      color: $white-color;
      font-weight: 700;
      letter-spacing: 0.5px;
      margin-top: 20px;
      margin-bottom: 10px;
      @include font-size(32);
    }
    .sub-title {
      font-weight: 400;
      @include font-size(14);
    }
  }
}
